@header-height: 70px;
@sidebar-width: 220px;
@sidebar-min-width: 80px;

//color
@primary: #0077ff;
@success: #36b389;
@warning: #f7a500;
@danger: #f94242;
@info: #498df2;

@bg: #fafbfc;
@border: #ebedf8;
@border-color: #ebedf8;

@font: #666;
@font-dark: #333;
@font-light: #999;
@font-lighter: #ccc;
@font-lightest: #e6e6e6;
//border
@border-solid: 1px @border solid;
@border-radius: 4px;

//size
@font-size: 14px;
@h1: 20px;
@h2: 18px;
@h3: 16px;
@h4: @font-size;
@h5: 12px;

//media
@sm: 768px;
@md: 1024px;
@lg: 1360px;

@table-header-bg: #f6f6f6;

